import './AdminPuntoRetiroUpdate.css'

import { IonAlert, IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import moment from 'moment'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { AdminItemField, DashboardModalComponent, Page, SelectorModal } from '../../components'
import { RUTAS, RUTAS_WEB_TRANSPORTISTA } from '../../constants'
import { openPageNow } from '../../helpers'
import {comprimirImagen, DetailViewProps, useToaster, useTryGoBack} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { PreviewImagenDatosTransportista, TipoVehiculo, Vehiculo } from '../../services/api'

export const AdminVehiculoUpdate: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { adminStore, loginStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [showAlert, setShowAlert] = useState(false)
  const [Errors, setErrors] = useState<Vehiculo | null>()
  const [Datos, setDatos] = useState<Vehiculo | null>()
  const [TipoVehiculo, setTipoVehiculo] = useState<TipoVehiculo>()
  const [IdInput, setIdInput] = useState('a')
  const [Previews, setPreviews] = useState<PreviewImagenDatosTransportista | null>()
  const DashaboardRef = useRef<any>()
  const IdVehiculo = match.params.id
  const EstadoVehiculo = match.params ? (match.params as any).estado : 'moderado'
  const tipos_vehiculos = rootStore.tiposVehiculos
  const [Deshabilitar, setDeshabilitar] = useState(false)
  const addToast = useToaster()
  const tryGoBack = useTryGoBack()
  // Confirm
  const [ConfirmTitulo, setConfirmTitulo] = useState('')
  const [ConfirmTexto, setConfirmTexto] = useState('')
  const [PorcentajeCompresion, setPorcentajeCompresion] = useState(0)
  const [InputComprimiendo, setInputComprimiendo] = useState('')

  const setup = async () => {
    obtenerTiposVehiculos()
    setDatos(null)
    setPreviews(null)
    if (
      IdVehiculo !== undefined &&
      history.location.pathname !== RUTAS_WEB_TRANSPORTISTA.VEHICULO_CREATE &&
      history.location.pathname !== RUTAS.ADMIN_VEHICULO_CREATE
    ) {
      obtenerVehiculo(parseInt(IdVehiculo))
    }
  }

  useEffect(() => {
    setup()
  }, [IdVehiculo])

  const obtenerTiposVehiculos = () => {
    rootStore.environment.api.tiposVehiculos().then((result) => {
      if (result.kind === 'ok') {
        // @ts-ignore
        rootStore.setTiposVehiculos(result.data.results)
      }
    })
  }

  const obtenerVehiculo = (idVehiculo: number) => {
    rootStore.environment.api.adminGetVehiculo(idVehiculo).then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        const hvencimiento = result.vehiculo.habilitacion_vencimiento
        const vehiculo = {
          ...result.vehiculo,
          habilitacion_vencimiento: hvencimiento ? moment(hvencimiento).format('DD/MM/YYYY') : '',
        }
        setDatos(vehiculo)
        setTipoVehiculo(result.vehiculo.tipo_vehiculo)
        setPreviews({
          poliza_seguro: result.vehiculo.poliza_seguro,
          habilitacion_frente: result.vehiculo.habilitacion_frente,
          habilitacion_dorso: result.vehiculo.habilitacion_dorso,
        })
      } else if (result.kind === 'not-found') {
        addToast('Vehículo no encontrado', 'warning')
        if (!loginStore.administrador) {
          openPageNow(RUTAS_WEB_TRANSPORTISTA.VEHICULOS, history)
        } else {
          openPageNow(RUTAS.ADMIN_VEHICULOS(EstadoVehiculo), history)
        }
      }
    })
  }

  const guardar = () => {
    setLoading(true)
    setErrors(null)
    let id_tipo_vehiculo = 0
    if (Datos) {
      if (Datos.tipo_vehiculo && typeof Datos.tipo_vehiculo !== 'number') {
        id_tipo_vehiculo = Datos.tipo_vehiculo.id
      } else {
        id_tipo_vehiculo = Datos.tipo_vehiculo
      }
    }
    let datos = { ...Datos, tipo_vehiculo: id_tipo_vehiculo }
    // @ts-ignore
    delete datos['transportista']
    if (
      (history.location.pathname === RUTAS.ADMIN_VEHICULO_CREATE ||
        history.location.pathname !== RUTAS.ADMIN_VEHICULO_CREATE) &&
      adminStore.transportistaDetalle
    ) {
      // @ts-ignore
      datos = { ...datos, transportista: adminStore.transportistaDetalle.id }
    }
    rootStore.environment.api.guardarVehiculo(datos, false).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Vehículo guardado exitosamente', 'success')
        if (loginStore.administrador) {
          tryGoBack(RUTAS.ADMIN_VEHICULO_DETAIL(EstadoVehiculo, result.vehiculo.id))
        } else {
          tryGoBack(RUTAS_WEB_TRANSPORTISTA.VEHICULO(result.vehiculo.id))
        }
      } else if (result.kind === 'bad-data') {
        setErrors(result.errors)
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const setDato = (campo: string, valor: any) => {
    let datos: Vehiculo
    // @ts-ignore
    datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const seleccionarInput = (id: string) => {
    setIdInput(id)
    DashaboardRef.current.toggle()
  }

  const dataSelectorModal = (data: any) => {
    setDato('tipo_vehiculo', data[0])
  }

  const file = async (data: any) => {
    const imagen_comprimida = await comprimirImagen(data, IdInput, compromiendoImagen)
    setInputComprimiendo(IdInput)
    // @ts-ignore
    setDato(IdInput, imagen_comprimida)
  }

  const compromiendoImagen = (porcentaje: number) => {
    setPorcentajeCompresion(porcentaje)
    if (porcentaje === 100) {
      setLoading(false)
      setTimeout(() => {
        setPorcentajeCompresion(0)
        setInputComprimiendo('')
      }, 500)
    } else {
      setLoading(true)
    }
  }

  const filePreview = (data: any) => {
    setPreview(IdInput, data)
    DashaboardRef.current.toggle()
  }

  const setPreview = (campo: string, valor: any) => {
    let previews: PreviewImagenDatosTransportista
    // @ts-ignore
    previews = { ...Previews }
    // @ts-ignore
    previews[campo] = valor
    setPreviews(previews)
  }
  const breadCrumbs = () => {
    if (loginStore.modo_uso_app === 30) {
      return (
        <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)}>Inicio</a>
          </li>
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS_WEB_TRANSPORTISTA.VEHICULOS)}>Vehículos</a>
          </li>
          <li className="breadcrumb-item active">{Datos ? 'Editar vehículo' : 'Crear vehículo'}</li>
        </ol>
      )
    } else {
      return (
        <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS.ADMIN)}>Inicio</a>
          </li>
          <li className="breadcrumb-item">
            <a onClick={() => history.push(RUTAS.ADMIN_VEHICULOS(EstadoVehiculo))}>Vehículos</a>
          </li>
          <li className="breadcrumb-item active">{Datos ? 'Editar' : 'Crear'}</li>
        </ol>
      )
    }
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  useEffect(() => {
    if (!loginStore.administrador) {
      if (IdVehiculo === undefined || IdVehiculo == '') {
        setDeshabilitar(true)
      } else {
        setDeshabilitar(false)
      }
    } else {
      setDeshabilitar(true)
    }
    if (IdVehiculo === undefined || IdVehiculo == '') {
      setConfirmTitulo('Crear vehículo')
      setConfirmTexto('El vehículo estará en estado pendiente de aprobación')
    } else {
      setConfirmTitulo('Editar vehículo')
      setConfirmTexto(
        'Al editar su vehículo este quedará pendiente de aprobación y no podrá utilizarlo, confirma que desea editar?',
      )
    }
  }, [IdVehiculo, Datos])

  return (
    <Page login_required admin>
      <IonAlert
        isOpen={showAlert}
        onDidDismiss={() => setShowAlert(false)}
        header={ConfirmTitulo}
        message={ConfirmTexto}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'btn-secondary',
          },
          {
            text: 'Guardar',
            cssClass: 'btn-primary',
            handler: () => {
              guardar()
            },
          },
        ]}
      />
      <DashboardModalComponent ref={DashaboardRef} id_input={IdInput} file={file} file_preview={filePreview} />
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">{Datos ? 'Editar vehículo' : 'Crear vehículo'}</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">{breadCrumbs()}</div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <div className={'row'}>
                <div className={'col-12 col-lg-6 col-xl-3'}>
                  <SelectorModal
                    titulo={'Tipo de vehículo'}
                    titulo_label={'Tipo de vehículo'}
                    listado_elementos={tipos_vehiculos}
                    listado_elementos_seleccionados={TipoVehiculo !== null ? TipoVehiculo : null}
                    datos_filtrados={dataSelectorModal}
                    multiple={false}
                    llave_label_opcion={'nombre'}
                    deshabilitado={Deshabilitar}
                  />
                  {Errors && !!Errors.tipo_vehiculo && (
                    <div className={'text-danger'}>Este campo no puede estar en blanco.</div>
                  )}
                </div>

                <AdminItemField
                  deshabilitado={Deshabilitar}
                  titulo={'Marca'}
                  tipo={'texto'}
                  campo={'marca'}
                  valor={Datos ? Datos.marca : ''}
                  set_dato={setDatoInput}
                  columnas={3}
                  error={Errors && Errors.marca}
                />

                <AdminItemField
                  deshabilitado={Deshabilitar}
                  titulo={'Modelo'}
                  tipo={'texto'}
                  campo={'modelo'}
                  valor={Datos ? Datos.modelo : ''}
                  set_dato={setDatoInput}
                  columnas={3}
                  error={Errors && Errors.modelo}
                />

                <AdminItemField
                  deshabilitado={Deshabilitar}
                  titulo={'Año'}
                  tipo={'texto'}
                  campo={'ano'}
                  valor={Datos ? Datos.ano : ''}
                  set_dato={setDatoInput}
                  columnas={3}
                  error={Errors && Errors.ano}
                />

                <AdminItemField
                  deshabilitado={Deshabilitar}
                  titulo={'Color'}
                  tipo={'texto'}
                  campo={'color'}
                  valor={Datos ? Datos.color : ''}
                  set_dato={setDatoInput}
                  columnas={3}
                  error={Errors && Errors.color}
                />

                <AdminItemField
                  deshabilitado={Deshabilitar}
                  titulo={'Número de chapa'}
                  tipo={'texto'}
                  campo={'numero_chapa'}
                  valor={Datos ? Datos.numero_chapa : ''}
                  set_dato={setDatoInput}
                  columnas={3}
                  error={Errors && Errors.numero_chapa}
                />

                <AdminItemField
                  deshabilitado={Deshabilitar}
                  titulo={'Número de chasis'}
                  tipo={'texto'}
                  campo={'numero_chassis'}
                  valor={Datos ? Datos.numero_chassis : ''}
                  set_dato={setDatoInput}
                  columnas={3}
                  error={Errors && Errors.numero_chassis}
                />
                <AdminItemField
                  deshabilitado={Deshabilitar}
                  titulo={'Kilometraje'}
                  tipo={'numero'}
                  campo={'kilometraje'}
                  valor={Datos ? Datos.kilometraje : ''}
                  set_dato={setDatoInput}
                  columnas={3}
                  error={Errors && Errors.kilometraje}
                />
                <AdminItemField
                  titulo={'Habilitación vencimiento'}
                  tipo={'fecha'}
                  campo={'habilitacion_vencimiento'}
                  valor={Datos ? Datos.habilitacion_vencimiento : ''}
                  set_dato={setDatoInput}
                  columnas={3}
                  error={Errors && Errors.habilitacion_vencimiento}
                />
                {loginStore.administrador && (
                  <AdminItemField
                    titulo={'Aprobado'}
                    tipo={'boolean'}
                    campo={'aprobado'}
                    valor={Datos ? Datos.aprobado : ''}
                    set_dato={setDatoInput}
                    columnas={3}
                  />
                )}
              </div>
              <div className={'row'}>
                <div className={'col-12 col-lg-6'}>
                  <div>
                    <label className={'font-weight-bold text-primary mt-2'}>
                      Habilitación frente{' '}
                      {InputComprimiendo === 'habilitacion_frente' && PorcentajeCompresion > 0
                        ? PorcentajeCompresion + '%'
                        : ''}
                    </label>
                    <button
                      className={'ml-4 btn btn-light btn-sm'}
                      title={'Subir foto del frente de la habilitación'}
                      onClick={() => seleccionarInput('habilitacion_frente')}
                    >
                      <FIcon.Camera size={14} />
                    </button>
                    {Errors && Errors.habilitacion_frente && (
                      <div className={'text-danger'}>{Errors.habilitacion_frente}</div>
                    )}
                  </div>
                  {Previews && Previews['habilitacion_frente'] && (
                    <img
                      className={'my-3'}
                      src={Previews['habilitacion_frente']}
                      style={{ maxHeight: 200, maxWidth: 200 }}
                      alt={'Habilitación frente'}
                    />
                  )}
                </div>
                <div className={'col-12 col-lg-6'}>
                  <div>
                    <label className={'font-weight-bold text-primary mt-2'}>
                      Habilitación dorso{' '}
                      {InputComprimiendo === 'habilitacion_dorso' && PorcentajeCompresion > 0
                        ? PorcentajeCompresion + '%'
                        : ''}
                    </label>
                    <button
                      className={'ml-4 btn btn-light btn-sm'}
                      title={'Subir foto del dorso de la habilitación'}
                      onClick={() => seleccionarInput('habilitacion_dorso')}
                    >
                      <FIcon.Camera size={14} />
                    </button>
                    {Errors && Errors.habilitacion_dorso && (
                      <div className={'text-danger'}>{Errors.habilitacion_dorso}</div>
                    )}
                  </div>
                  {Previews && Previews['habilitacion_dorso'] && (
                    <img
                      className={'my-3'}
                      src={Previews['habilitacion_dorso']}
                      style={{ maxHeight: 200, maxWidth: 200 }}
                      alt={'Poliza de seguro'}
                    />
                  )}
                </div>
                <div className={'col-12 col-lg-6'}>
                  <div>
                    <label className={'font-weight-bold text-primary mt-2'}>
                      Póliza de seguro{' '}
                      {InputComprimiendo === 'poliza_seguro' && PorcentajeCompresion > 0
                        ? PorcentajeCompresion + '%'
                        : ''}
                    </label>
                    <button
                      className={'ml-4 btn btn-light btn-sm'}
                      title={'Subir foto del frente del registro'}
                      onClick={() => seleccionarInput('poliza_seguro')}
                    >
                      <FIcon.Camera size={14} />
                    </button>
                    {Errors && Errors.poliza_seguro && <div className={'text-danger'}>{Errors.poliza_seguro}</div>}
                  </div>
                  {Previews && Previews['poliza_seguro'] && (
                    <img
                      className={'my-3'}
                      src={Previews['poliza_seguro']}
                      style={{ maxHeight: 200, maxWidth: 200 }}
                      alt={'Poliza de seguro'}
                    />
                  )}
                </div>
              </div>
              <div className={'form-group text-right'}>
                <a onClick={() => tryGoBack(RUTAS.ADMIN_VEHICULOS((EstadoVehiculo)))} className={'btn btn-light'}>
                  Cancelar
                </a>
                <button
                  className={'btn btn-primary ml-2'}
                  disabled={Loading}
                  onClick={() => {
                    if (loginStore.modo_uso_app === 30) {
                      setShowAlert(true)
                    } else {
                      guardar()
                    }
                  }}
                >
                  {Loading && <IonSpinner name="crescent" />}
                  {!Loading && <span>Guardar</span>}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminVehiculoUpdate
